import { Component, OnInit } from '@angular/core';
import { LocalDataService } from 'src/app/shared/service/local-data.service';
import { HierarchyService } from '../../../shared/components/hierarchy/hierarchy.service';

@Component({
  selector: 'app-staff-view',
  templateUrl: './staff-view.component.html',
  styleUrls: ['./staff-view.component.scss']
})
export class StaffViewComponent implements OnInit {

  node = [
    { id: 11, name: 'Stanley Chu', parent_id: null },
    { id: 1, name: 'Darius Bualan', parent_id: 11 },
    { id: 2, name: 'Jener Sigua', parent_id: 1 },
    { id: 3, name: 'Cherry Ho', parent_id: 1 },
    { id: 22, name: 'Joe Evans', parent_id: 3 },
    { id: 32, name: 'David Ng', parent_id: 2 },
  ];
  public localData = this.localDataService.data;

  mode = 'view';
  isSuperAdmin = false;

  constructor(
    private hierarchyService: HierarchyService,
    private localDataService: LocalDataService
  ) { }

  ngOnInit(): void {
    this.node = this.hierarchyService.formatOrg(this.node);
    this.isSuperAdmin = JSON.parse(localStorage.getItem('user'))?.type === 1;
  }

  edit(): void{
    this.mode = 'edit';
  }

  cancel(): void{
    this.mode = 'view';
  }

  save(): void {
    this.mode = 'view';
  }
}
